
body {
  min-width: 375px;
}
.panel {
  width: 375px;
  margin: 15px auto;
}
.breadcrumb {
  margin-bottom: 10px;
}
@media screen and (max-width: 480px) {
.panel {
    width: 100%;
    margin: 0;
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}
}
.submit,
img {
  width: 100%;
}
.bottom {
  border-top: 1px dashed #ccc;
  padding-top: 15px;
  text-align: center;
}



